import { Component, ChangeDetectionStrategy, ViewChild, ChangeDetectorRef } from "@angular/core";
import { Platform, Form, ViewController, ModalController } from "ionic-angular";


import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsAuthService } from "@services/auth.service";
import { Backend } from "@backend/index";


@Component({
    selector: "page-select-bank-account",
    templateUrl: "select-bank-account.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class SelectBankAccountPage {

    translations: AppTranslations;
    token: string;
    bankAccounts: Array<any>;
    withdrawalLoaded: boolean;
    availableWithdrawal: any;
    futureWithdrawal: any;

    constructor(private platform: Platform,
        private viewCtrl: ViewController,
        private modalCtrl: ModalController,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private interactionService: MarsInteractionService,
        private globals: AppGlobals) {
        this.translations = this.locales.load();
    }

    ionViewWillEnter() {
        this.init();
    }

    async init() {
        if (!MarsAuthService.hasRole('MERCHANT')) return;
        this.token = MarsAuthService.getMarsToken();
        this.getAvailableWithdrawal();
        this.getBankAccounts();
    }

    ionViewDidEnter() { }


    async getAvailableWithdrawal() {
        try {
            let response = (await Backend.getAvailabeWithdrawal({ xAccessToken: this.token })).data;
            this.availableWithdrawal = response.current;
            this.futureWithdrawal = response.future;
            this.withdrawalLoaded = true;
            this.changeDetector.detectChanges();
        } catch (e) {
            
        }
    }

    async getBankAccounts() {
        try {
            let bankAccounts = (await Backend.getBankAccounts({ xAccessToken: this.token })).data;
            this.bankAccounts = bankAccounts;
            this.changeDetector.detectChanges();
        } catch (e) {
            
        }
    }

    async select(account) {
        this.dismiss(account);
    }

    dismiss(data?) {
        this.viewCtrl.dismiss(data);
    }
}



